/* sample.css */
.lms .g-info .ui.label.user span.name ,
.lms .g-info .ui.label.user span.affiliation{
  display: none ;
}

#root section.header.lms .group .g-search {
  position: absolute;
  top: 0;
  left: 859px;
  width: 198px;
  height: inherit;
  border-left: solid 1px #eee;
}
#root section.header.lms .group .g-search .ui.input {
  width: 100%;
  display: -ms-inline-flex-box;
  display: inline-block;
  margin-top: 19px;
  margin-left:30px;
}
#root section.header.lms .group .g-info {
  position: absolute;
  right: 45px;
  top: 3px;
  width: 40px;
  height: 100%;
  border-left: none;
}
#root section.header.lms .group .g-info .ui.label.user{
  width: auto !important;
  padding: 0 39px 0 0;
}
.lms .g-info .ui.label.user img {
  position: absolute;
  left: 0;
  top: 0;
  width: 2.375rem !important;
  height: 2.375rem !important;
  margin: 0;
  border-radius: 100%;
  border: 0.0625rem solid #ddd;
}
.lms-alarm{
  text-indent: 110%;
  white-space: nowrap;
  overflow: hidden;
  display: inline-block;
  background: url('../images/all/btn-gnb-notice.svg') center no-repeat;
  width: 36px;
  height: 36px;
  background-size: 36px;
  display: none;
  position: absolute;
  right: -43px;
  top: 16px;
}
.lms .lms-alarm{display: block}
.lms-alarm.lms-on:after{
  content: "";
  display: block;
  width: 4px;
  height: 4px;
  background-color: #e1002a;
  position: absolute;
  border-radius: 2px;
  right: 4px;
  top: 4px;
}
.lms .right{
  display: none;
}
.lms .lms-right{
  position: absolute;
  right: 0;
  top: 0;
}
.lms .lms-right .btn-black{
 font-weight: normal;
  text-decoration: underline;
  vertical-align: middle;
  font-size: 12px;
  height: 32px;
}
.lms .lms-right .ui.button.btn-black .icon.morelink {
  background: url('../images/all/icon-community-jump-8-px.svg'), 50% 0 no-repeat;
}